
.layout-parent {
    display: flex;
    height: 100%;
    width: 100%;
    padding: 15px;
    height: 100vh;
    gap: 20px;
    overflow-x: scroll !important;
  }
  
  .body {
    width: 100%;
  }
  .header {
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.479);
    width: 100%;
    padding: 20px;
  }
  
  .sidebar {
    background-color: var(--secondary);
    border-radius: 5px;
    display: flex;
    justify-content: start;
    padding: 0 20px;
    flex-direction: column;
    
  }
  .logo {
    color: white;
  }
  .role {
    color: white;
    font-size: 16px;
  }
  .menu{
      margin-top: 150px;
  }
  .menu-item {
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 5px 15px;
    width: 100%;
    cursor: pointer;
    transition: 0.2s;
    gap: 15px;
  }
  .active-menu-item {
    border: 2px solid white;
    border-radius: 5px;
  }
  .menu-item i {
    font-size: 20px;
    margin-right: 10px;
    color: white;
  }
  .menu-item span {
    color: white;
    font-size: 18px;
  }
  .content {
    padding: 10px 20px;
    overflow-y: auto !important;
    overflow-x: auto !important;
    height: 85vh;
  }


  .flex-head{
    display: flex;
   
    align-items: center;
  }
  .head-top{
    margin:auto;
    font-weight: 700;
    font-size: 20px;
    background-color: var(--secondary);
    padding-left: 40%;   /* Adjust the percentage as per your requirement */
  padding-right: 40%;
  padding-top: 1%;   /* Adjust the percentage as per your requirement */
  padding-bottom: 1%;
  border-radius: 10px 10px 10px 10px;
  color: white;
  
  }
  .table-container {
    /* Add some basic styling to the table container */
    padding: 16px;
  }
  
  /* Media query for small screens */
  @media (max-width: 767px) {
    .table-container {
      /* Adjust table container styles for small screens */
      overflow-x: auto; /* Add horizontal scrollbar when content overflows */
    }
  }
  .topclose{
    margin-left:15px;
  }